<template>
    <section id="game_theme"></section>
</template>

<script>
export default {
    props: ['subject'],
    data(){
        return{

        }
    },
    created(){
        if(this.subject==true){
            this.changeMeans();
        }
    },
    methods: {
        openFullScreen() {
            return this.$loading({
            lock: true,
            text: 'Loading',
            spinner: 'el-icon-loading',
            background: 'rgba(0, 0, 0, 0.7)'
            });
        },
        changeMeans(){
            // //console.log('subject######');
            this.variableChange1();
            this.variableChange2();
        },
        variableChange1(){
            var that = this;
            Object.defineProperty(sceneWeb, 'type', {
                get: function() {
                    return 'type';
                },
                set: function(value) {
                    //console.log('changjing%%%%%',value,that.$route.path);
                    if(that.$route.path=='/register'){
                        return;
                    }
                    if(value=='999992'){
                        that.$router.push({path: '/home'});
                        web.game_exit_scene(function(value){});
                    }else if(value=='999991'||value=='999993'||value=='999994'||value=='999995'){
                        that.$router.push({path: '/game'});
                        that.openFullScreen().close();
                    }
                }
            });
        },
        variableChange2(){
            var that = this;
            Object.defineProperties(errorCodeGame,{
                code:{
                    configurable: true,
                    get:function(){

                    },
                    set:function(value){
                        if(value==1001||value==1002||value==1101||value==1102||value==1103||value==1104||value==1105||value==1201||value==1203||value==1204||value==1301||value==1302||value==1303||value==1||value==2){
                            that.$router.push({path: '/home'});
                            that.openFullScreen().close();
                            web.game_exit_scene(function(value){});
                        }else if(value==1202){
                            that.$router.push({path: '/login'});
                        }
                    }
                },
                overtime:{
                    configurable: true,
                    get:function(){

                    },
                    set:function(value){
                        that.$router.push({path: '/home'});
                        that.openFullScreen().close();
                        web.game_exit_scene(function(value){});
                        // that.$message.error('!');
                    }
                },
                move:{
                    configurable: true,
                    get:function(){

                    },
                    set:function(value){
                        that.$emit('interaction',value);
                    }
                },
                refresh:{
                    configurable: true,
                    get:function(){

                    },
                    set:function(value){
                        that.$emit('interaction',value);
                        if(sessionStorage.getItem('sign_in')&&sessionStorage.getItem('register')){
                            catchGame.mice = 'ok';
                            sessionStorage.removeItem('sign_in');
                            sessionStorage.removeItem('register');
                            return;
                        }else if(sessionStorage.getItem('sign_in')){
                            catchGame.mice = 'ok';
                            sessionStorage.removeItem('sign_in');
                        }else if(sessionStorage.getItem('register')){
                            catchGame.register = 'ok';
                            sessionStorage.removeItem('register');
                        }
                    }
                },
                socket:{
                    configurable: true,
                    get:function(){

                    },
                    set:function(value){
                        that.$confirm('', '', {
                            confirmButtonText: '',
                            cancelButtonText: '',
                            type: 'warning'
                        }).then(() => {
                            web.game_relogin();
                        }).catch(() => {
                            web.game_clear();
                        });
                    }
                },
                description:{
                    configurable: true,
                    get:function(){

                    },
                    set:function(value){
                        that.$alert(value, '', {
                            confirmButtonText: '',
                            type: 'error',
                            callback: action => {

                            }
                        });
                    }
                },
                recharge:{
                    configurable: true,
                    get:function(){

                    },
                    set:function(value){
                        that.$alert('', '', {
                            confirmButtonText: '',
                            type: 'success',
                            callback: action => {
                                web.game_getPlayer().setMoney(value);
                                that.$store.dispatch('getPlayerInfo',web.game_getPlayer());
                            }
                        });
                    }
                }
            });
        }
    }
}
</script>

<style lang="less" scoped>
#game_theme{
    width: 100%;
    height: 0px;
}
</style>


