<template>
    <div id="app">
        <router-view></router-view>
        <game-theme ref="subject" :subject="false" v-show="subject"></game-theme>
    </div>
</template>

<script>
export default {
    name: 'App',
    data(){
        return{
            subject: true
        }
    },
    watch: {
        $route(to, from){
            if(to.path == '/home'||to.path == '/neutralgear'){
                this.subject = false;
            }else{
                this.subject = true;
                this.$refs.subject.changeMeans();
            }
            // console.log('watch~~~',to,from);
            if(to.path == '/game'){
                purpleMagic(3);
                window.dispatchEvent(new Event('resize'));
            }else{
                purpleMagic(2);
            }
            
            if(from.path == '/register'){
              //console.log('');
              let that = this;
              web.game_register_exit();
              // web.game_exit(function(){});
              // web.game_exit_scene(function(value){});
            }
        }
    },
    created(){
      // console.log('home@@@@@111',this.$router.path);
    }
}
</script>


<style lang="less">
@import './assets/app.less';
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
/*--*/
html, body, div, ul, li, h1, h2, h3, h4, h5, h6, p, dl, dt, dd, ol, form, input, textarea, th, td, select {
  margin: 0;
  padding: 0;
}
*{box-sizing: border-box;}
html, body {
  min-height: 100%;
  height:100% !important;
}

body {
  font-family: "Microsoft YaHei" !important;
  font-size:14px !important;
  color:#333 !important;
}
h1, h2, h3, h4, h5, h6{font-weight:normal;}
ul, ol, li{
  list-style: none;
}
img {
  border: none;
  vertical-align: middle;
}

a {
  text-decoration: none;
  color: #232323;
}

table {
  border-collapse: collapse;
  table-layout: fixed;
}

input, textarea,button{
  outline: none;
  border: none;
}

textarea {
  resize: none;
  overflow: auto;
}

</style>



